<template>
  <header id="header">
    <!-- header area -->
    <h1 id="top_logo">
      <a href="/">Xwood</a>
      <router-link to="/"></router-link>
    </h1>
    <nav id="top_nav">
      <ul>
        <li v-for="(list,index) in menuList">
          <router-link :to="list.ahref">{{list.text}}</router-link>
          <ul v-if="index == 0" class="submenu">
            <li v-for="list in companySubList">
              <router-link :to="list.ahref">{{list.text}}</router-link>
            </li>
          </ul>
          <ul v-if="index == 3" class="submenu">
            <li v-for="list in noticeSubList">
              <router-link :to="list.ahref">{{list.text}}</router-link>
            </li>
          </ul>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
export default {
  name: 'HeaderComponent',
  data () {
    return {
      menuList: [
        {text: 'Company', ahref: '/company'},
        {text: 'Product', ahref: '/product'},
        {text: 'gallery', ahref: '/gallery'},
        {text: 'notice', ahref: '/notice'},
        {text: 'location', ahref: '/location'},
      ],
      companySubList: [
        {text: 'introduce', ahref: '/introduce'},
        {text: 'ci', ahref: '/ci'},
        {text: 'history', ahref: '/history'},
      ],
      noticeSubList: [
        {text: 'notice', ahref: '/notice'},
        {text: 'faq', ahref: '/faq'}
      ]
    }
  }
}
</script>



